<template>
  <ul>
    <template v-for="x in categories" :key="x.postCategoryId">
      <li class="d-flex">
        <span class="fa fa-edit cursor-pointer text-info"
              @click="$emit('update',{postCategoryId:x.postCategoryId,categoryName: x.categoryName})"
        ></span>

<!--        <span class="fa fa-trash ms-2 cursor-pointer text-danger" @click="$emit('delete',x)"></span>-->
        <span class="fa fa-code-branch ms-2 cursor-pointer text-success" title="اضافه کردن زیر مجموعه"
              @click="$emit('addChild',{parentId : x.postCategoryId})"
        ></span>

        <div class="ms-2">{{ x.categoryName }}</div>
      </li>

      <categories-list-view :categories="x.children"
                            @delete="$emit('delete',$event)"
                            @addChild="$emit('addChild',$event)"
                            @update="$emit('update',$event)"
      />
    </template>
  </ul>
</template>

<script>
export default {
  name: 'CategoriesListView',
  props: {
    categories: { default: [] }
  }
}
</script>

