<template>
  <div>
    <page-title/>

    <div class="panel">
      <button class="btn btn-success" type="button"
              @click="getFormModal().show()"
      >
        <span class="fa fa-plus me-2"></span>
        <span>ثبت گروه جدید</span>
      </button>

      <button class="btn btn-primary ms-2" type="button"
              @click="retrieveCategories"
      >
        <span class="fa fa-sync me-2" :class="{'fa-spin' : working}"></span>
        <span>بروز رسانی لیست</span>
      </button>
    </div>

    <div class="mt-3 panel">
      <categories-list-view :categories="showFilter()"
                            @delete="onDelete($event)"
                            @addChild="showForm($event)"
                            @update="showForm($event)"/>
    </div>


    <div class="modal fade" id="categoryFormModal" tabindex="-1" aria-labelledby="categoryFormModalLabel"
         aria-hidden="true"
    >
      <form @submit.prevent="onFormSubmit" class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="categoryFormModalLabel">ثبت گروه جدید</h5>
            <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
          </div>
          <div class="modal-body">

            <div>
              <label class="form-label">عنوان</label>
              <input type="text" class="form-control" v-model="model.categoryName"/>
            </div>

            <ul class="mt-3 text-danger">
              <li v-for="(x,k) in errors" :key="k">
                {{ x }}
              </li>
            </ul>

          </div>
          <div class="modal-footer">
            <button type="submit" class="btn btn-success">ذخیره تغییرات</button>
            <button type="button" class="btn btn-warning" data-bs-dismiss="modal">انصراف</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import PageTitle from '../../tools/PageTitle'
import { dispatchNotification } from '../../library/stores/notifications'
import CategoriesListView from './categories/CategoriesListView'

export default {
  name: 'Categories',
  components: {
    CategoriesListView,
    PageTitle
  },
  data: () => ({
    working: false,
    categories: [],
    model: {
      categoryName: ''
    },
    errors: {},
    formModal: undefined
  }),
  methods: {
    retrieveCategories() {
      this.working = true
      this.$fetch('/postcategory', 'get')
          .then(x => {
            this.categories = x.data
          })
          .finally(() => this.working = false)
    },
    getFormModal() {
      if (!this.formModal) {
        this.formModal = new window.bootstrap.Modal(document.getElementById('categoryFormModal'))
      }
      return this.formModal
    },
    showForm(category) {
      this.model = {
        parentId: category.parentId,
        postCategoryId: category.postCategoryId,
        categoryName: category.categoryName
      }
      this.getFormModal()
          .show()
    },
    onFormSubmit() {
      if (this.working) {
        return
      }
      this.errors = {}
      if (!this.model.categoryName.trim()) {
        this.errors.categoryName = 'عنوان گروه وارد نشده است'
      }

      if (Object.keys(this.errors).length > 0) {
        return false
      }

      this.working = true
      this.$fetch('/postcategory',
          this.model.postCategoryId ? 'patch' : 'post', this.model)
          .then(() => {
            this.retrieveCategories()
            dispatchNotification('گروه با موفقیت اضافه شد')
            this.getFormModal()
                .hide()
            this.model = {}
          })
          .catch(x => this.errors = x.data)
          .finally(() => this.working = false)
    },
    showFilter(parentId = null) {
      return this.categories.filter(x => x.parentId === parentId)
    },
    onDelete(category) {
      if (category && category.categoryName) {
        if (confirm(`آیا از حذف ${category.categoryName} اطمینان دارید؟`)) {
          this.working = true

        }
      }
    }
  },
  mounted() {
    this.retrieveCategories()
  }
}
</script>

